import type { 
  Chapter, 
  Concept, 
  ConceptMap, 
  Flashcard, 
  Question, 
  ProcessedContent,
  ELI5Summary,
  VisualAid,
  AIProcessingOptions
} from '../types';

// ============================================================================
// AI SERVICE - DEEP COMPREHENSION ENGINE
// ============================================================================

// API Key Management
const API_KEY_STORAGE_KEY = 'tavily_api_key';

export function getApiKey(): string | null {
  return localStorage.getItem(API_KEY_STORAGE_KEY);
}

export function setApiKey(key: string): void {
  localStorage.setItem(API_KEY_STORAGE_KEY, key);
}

export function removeApiKey(): void {
  localStorage.removeItem(API_KEY_STORAGE_KEY);
}

export function hasApiKey(): boolean {
  return !!getApiKey();
}

// ============================================================================
// MAIN PDF PROCESSING FUNCTION
// ============================================================================

export async function processPDFWithAI(
  pdfText: string, 
  options: AIProcessingOptions = {
    useELI5: true,
    generateVisuals: true,
    generateConceptMap: true,
    difficulty: 'beginner'
  }
): Promise<ProcessedContent> {
  const apiKey = getApiKey();
  
  if (apiKey && pdfText.length > 100) {
    try {
      // Try enhanced AI processing with Tavily
      return await processWithTavilyAI(pdfText, apiKey, options);
    } catch (error) {
      console.warn('Tavily AI processing failed, using enhanced local processing:', error);
      return processPDFLocally(pdfText, options);
    }
  }
  
  // Use enhanced local processing
  return processPDFLocally(pdfText, options);
}

// ============================================================================
// TAVILY AI INTEGRATION
// ============================================================================

async function processWithTavilyAI(
  pdfText: string,
  apiKey: string,
  options: AIProcessingOptions
): Promise<ProcessedContent> {
  // Chunk the content intelligently by concepts
  const chunks = chunkByConcepts(pdfText);
  
  const chapters: Chapter[] = [];
  const allConcepts: Concept[] = [];
  const allFlashcards: Flashcard[] = [];
  const allQuestions: Question[] = [];
  const allVisualAids: VisualAid[] = [];

  for (let i = 0; i < chunks.length; i++) {
    const chunk = chunks[i];
    
    // Process each chunk with AI
    const prompt = buildProcessingPrompt(chunk, options);
    
    try {
      const response = await fetch('https://api.tavily.com/search', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          api_key: apiKey,
          query: prompt,
          search_depth: 'advanced',
          include_answer: true,
          max_results: 5,
        }),
      });

      if (!response.ok) throw new Error('Tavily API request failed');

      const data = await response.json();
      const processedChunk = parseAIResponse(data.answer || '', chunk, i + 1);
      
      chapters.push(processedChunk.chapter);
      allConcepts.push(...processedChunk.concepts);
      allFlashcards.push(...processedChunk.flashcards);
      allQuestions.push(...processedChunk.questions);
      allVisualAids.push(...processedChunk.visualAids);
      
    } catch (error) {
      // Fall back to local processing for this chunk
      const localResult = processChunkLocally(chunk, i + 1, options);
      chapters.push(localResult.chapter);
      allConcepts.push(...localResult.concepts);
      allFlashcards.push(...localResult.flashcards);
      allQuestions.push(...localResult.questions);
      allVisualAids.push(...localResult.visualAids);
    }
  }

  // Build concept map
  const conceptMap: ConceptMap = options.generateConceptMap 
    ? buildConceptMap(chapters[0]?.id || 'default', allConcepts)
    : { id: crypto.randomUUID(), chapterId: chapters[0]?.id || 'default', concepts: [], connections: [] };

  return {
    chapters,
    concepts: allConcepts,
    conceptMap,
    flashcards: allFlashcards,
    questions: allQuestions,
    visualAids: allVisualAids,
  };
}

// ============================================================================
// LOCAL PROCESSING (Enhanced)
// ============================================================================

function processPDFLocally(
  pdfText: string,
  options: AIProcessingOptions
): ProcessedContent {
  const chunks = chunkByConcepts(pdfText);
  
  const chapters: Chapter[] = [];
  const allConcepts: Concept[] = [];
  const allFlashcards: Flashcard[] = [];
  const allQuestions: Question[] = [];
  const allVisualAids: VisualAid[] = [];

  for (let i = 0; i < chunks.length; i++) {
    const result = processChunkLocally(chunks[i], i + 1, options);
    chapters.push(result.chapter);
    allConcepts.push(...result.concepts);
    allFlashcards.push(...result.flashcards);
    allQuestions.push(...result.questions);
    allVisualAids.push(...result.visualAids);
  }

  const conceptMap: ConceptMap = options.generateConceptMap
    ? buildConceptMap(chapters[0]?.id || 'default', allConcepts)
    : { id: crypto.randomUUID(), chapterId: chapters[0]?.id || 'default', concepts: [], connections: [] };

  return {
    chapters,
    concepts: allConcepts,
    conceptMap,
    flashcards: allFlashcards,
    questions: allQuestions,
    visualAids: allVisualAids,
  };
}

function processChunkLocally(
  chunk: string,
  chapterNum: number,
  options: AIProcessingOptions
): {
  chapter: Chapter;
  concepts: Concept[];
  flashcards: Flashcard[];
  questions: Question[];
  visualAids: VisualAid[];
} {
  // Detect chapter topic
  const topic = detectTopic(chunk);
  
  // Generate ELI5 content
  const eli5Summary = generateELI5Summary(chunk, topic);
  
  // Extract concepts
  const concepts = extractConcepts(chunk, topic);
  
  // Generate flashcards from concepts
  const flashcards = generateEnhancedFlashcards(concepts, chapterNum.toString());
  
  // Generate questions
  const questions = generateEnhancedQuestions(concepts, chapterNum.toString(), topic);
  
  // Generate visual aids
  const visualAids = options.generateVisuals 
    ? generateVisualAids(concepts, topic)
    : [];

  const chapter: Chapter = {
    id: crypto.randomUUID(),
    bookId: 'temp',
    number: chapterNum,
    title: extractTitle(chunk) || `Chapter ${chapterNum}`,
    content: chunk,
    eli5Summary,
    concepts,
    visualAids,
    readingTime: Math.ceil(chunk.length / 1000),
    difficulty: options.difficulty === 'beginner' ? 'easy' : options.difficulty === 'intermediate' ? 'medium' : 'hard',
    topics: [topic],
    createdAt: new Date(),
    updatedAt: new Date(),
  };

  return { chapter, concepts, flashcards, questions, visualAids };
}

// ============================================================================
// CONTENT CHUNKING - Intelligent concept-based chunking
// ============================================================================

function chunkByConcepts(text: string): string[] {
  // Split by chapter patterns
  const chapterPatterns = [
    /Chapter\s+\d+[:\s]+/i,
    /CHAPTER\s+\d+[:\s]+/i,
    /^\d+\.\s+[A-Z][^\n]+/m,
    /\n\n[A-Z][A-Z\s]{3,}[\n\r]/,
  ];

  const chunks: string[] = [];
  let currentChunk = '';
  const lines = text.split('\n');

  for (const line of lines) {
    let isNewChapter = false;
    
    for (const pattern of chapterPatterns) {
      if (pattern.test(line)) {
        if (currentChunk.trim()) {
          chunks.push(currentChunk.trim());
        }
        currentChunk = line + '\n';
        isNewChapter = true;
        break;
      }
    }

    if (!isNewChapter) {
      currentChunk += line + '\n';
    }
  }

  if (currentChunk.trim()) {
    chunks.push(currentChunk.trim());
  }

  // If no chapters detected, split into semantic chunks
  if (chunks.length === 0 || (chunks.length === 1 && chunks[0].length > 5000)) {
    return splitIntoSemanticChunks(text);
  }

  return chunks.length > 0 ? chunks : [text];
}

function splitIntoSemanticChunks(text: string): string[] {
  const chunks: string[] = [];
  const sentences = text.match(/[^.!?]+[.!?]+/g) || [text];
  let currentChunk = '';
  const targetSize = 2000;

  for (const sentence of sentences) {
    if (currentChunk.length + sentence.length > targetSize && currentChunk.length > 500) {
      chunks.push(currentChunk.trim());
      currentChunk = sentence;
    } else {
      currentChunk += sentence + ' ';
    }
  }

  if (currentChunk.trim()) {
    chunks.push(currentChunk.trim());
  }

  return chunks.length > 0 ? chunks : [text];
}

// ============================================================================
// ELI5 CONTENT GENERATION
// ============================================================================

function generateELI5Summary(_content: string, topic: string): ELI5Summary {
  const summaries: Record<string, ELI5Summary> = {
    'real-estate': {
      oneSentence: 'Real estate is about buying, selling, and using land and buildings.',
      shortSummary: 'Real estate involves land and everything permanently attached to it. It includes homes, offices, farms, and factories. Understanding real estate helps people make smart decisions about property.',
      whyItMatters: 'Real estate is often the biggest purchase people make in their lifetime. Whether buying a home or investing, understanding real estate protects your money.',
      realWorldConnection: 'When you buy a house, rent an apartment, or see a new shopping center being built - that\'s all real estate in action.'
    },
    'ownership': {
      oneSentence: 'Property ownership gives you special rights to use and control land and buildings.',
      shortSummary: 'When you own property, you get a "bundle of rights" - like a toolbox full of permissions. You can live there, rent it out, sell it, or even borrow money using it as collateral.',
      whyItMatters: 'Understanding ownership helps you protect your property rights and know what you can and cannot do with your property.',
      realWorldConnection: 'When neighbors disagree about property lines, or when someone wants to build a fence, knowing ownership rights helps solve these problems.'
    },
    'finance': {
      oneSentence: 'Real estate finance is about borrowing money to buy property and paying it back over time.',
      shortSummary: 'Most people can\'t pay cash for a house, so they get a loan called a mortgage. You pay a little bit each month - some goes to pay back what you borrowed, and some is interest (the cost of borrowing).',
      whyItMatters: 'Understanding finance helps you get the best loan, save money on interest, and avoid losing your home if you can\'t make payments.',
      realWorldConnection: 'When you hear about interest rates going up or down, that affects how much house people can afford and what their monthly payments will be.'
    },
    'contracts': {
      oneSentence: 'A real estate contract is a written promise between people about buying or selling property.',
      shortSummary: 'Contracts are like a rulebook for a real estate deal. They spell out who does what, when, and for how much money. Once signed, everyone must follow the rules or face consequences.',
      whyItMatters: 'Contracts protect everyone in a deal. They make sure the buyer gets the property and the seller gets paid, with clear steps along the way.',
      realWorldConnection: 'When you see a "For Sale" sign come down and people moving in, a contract made that transition happen smoothly and legally.'
    },
    'agency': {
      oneSentence: 'A real estate agent is a licensed professional who helps people buy or sell property.',
      shortSummary: 'Agents are like expert guides in the real estate world. They know the market, handle paperwork, negotiate deals, and look out for their client\'s best interests.',
      whyItMatters: 'Good agents save you time, money, and stress. They help you avoid costly mistakes and find the best deals.',
      realWorldConnection: 'When you see open houses or yard signs with agent names, those professionals are working to match buyers with their perfect property.'
    },
    'default': {
      oneSentence: 'This chapter covers important concepts you need to know for your real estate exam.',
      shortSummary: 'This material builds your foundation as a real estate professional. Take time to understand each concept before moving on.',
      whyItMatters: 'Real estate professionals help people make life-changing decisions. Your knowledge protects your clients and your career.',
      realWorldConnection: 'Every concept you learn applies to real situations you\'ll encounter working with buyers, sellers, and properties.'
    }
  };

  return summaries[topic] || summaries['default'];
}

